import React, { Component } from "react";
import {
  FacebookShareButton,
  FacebookIcon,
  TwitterShareButton,
  TwitterIcon,
} from "react-share";
import axios from "axios";
import clsx from "clsx";
import { withStyles } from "@material-ui/core";
import PropTypes from "prop-types";
import CookieBannerContainer from "./components/CookieBanner/CookieBannerContainer";
import DataLayer from "./components/DataLayer";
import Map from "./components/Map";
import FeatureInfo from "./components/FeatureInfo";
import Legende from "./components/Legende";
import ListCountries from "./components/ListCountries/ListCountries";
import SideBar from "./components/SideBar/SideBar";
import SideBarButton from "./components/SideBarButton/SideBarButton";
import Snackbar from "./components/Snackbar";
import TotalInfo from "./components/TotalInfo";
import {
  getFilterData,
  getMapData,
  parseRawData,
} from "./structs/utils";
import {sidebarWidth} from "./settings";
import "./App.scss";


// Import globals
const PRIVACY_LINK = process.env.REACT_APP_LINK_PRIVACY;
const IMPRESSUM_LINK = process.env.REACT_APP_LINK_IMPRESSUM;
const PUBLIC_URL = process.env.PUBLIC_URL;

const styles = (theme) => {
  return {
    containerMap: {
      width: "100%",
      height: "100%",
      display: "block",
    },

    content: {
      flexGrow: 1,
      transition: theme.transitions.create('margin', {
        easing: theme.transitions.easing.sharp,
        duration: theme.transitions.duration.leavingScreen,
      }),
      marginLeft: -sidebarWidth,
      marginBottom: 40,
    },
    root: {
      height: "100%",
      width: "100%",
      display: "flex",
    },
    footer: {
      position: "absolute",
      left: 0,
      right: 0,
      bottom: 0,
      height: 40,
      background: "#F5F5F5",
      "& div": {
        marginTop: theme.spacing(1.5),
        textAlign: "center",
      },
      "& a": {
        height: 16,
        width: 64,
        color: "rgba(0,0,0,0.6)",
        fontFamily: "\"Roboto\", \"Helvetica\", \"Arial\", sans-serif",
        fontSize: 14,
        letterSpacing: 0.4,
        lineHeight: "18px",
        textAlign: "center",
        textDecoration: "none",
      },
      "& a:hover": {
        color: "rgba(0,0,0,0.6)",
        textDecoration: "none",
      },
      "& a:first-child": {
        paddingRight: theme.spacing(3)
      },
    }
  }
};

class App extends Component {
  state = {
    // parameters for the size of the map container
    width: 0,
    height: 0,

    // Which product category is currently selected. "All" means every product / total.
    categorySelected: "All",

    // map object
    map: undefined,

    // base map data
    mapData: undefined,

    // base metadata data
    metadata: undefined,

    // use this in case a feedback is need
    feedback: undefined,

    // features hover/select
    hoverFeature: undefined,
    selectFeature: undefined,

    sidebarOpen: true,

    // Show euro or tonnes
    useEuro: true,

    // Show import
    showImport: true,

    // data selection currently displayed
    visData: undefined,
  };

  componentDidMount() {
    window.addEventListener("resize", this.handleResize);

    // Initial make sure to update the size parameters
    this.handleResize();

    // Start fetching the relevant data for visualization
    this.fetchInitialData();
  }

  componentWillUnmount() {
    window.removeEventListener("resize", this.handleResize);
  }

  fetchInitialData = async () => {
    try {
      const mapData = await axios.get("data/countries.json");
      const dataSummary = await axios.get("data/data-summary.json");
      const dataRaw = await axios.get("data/data-raw.csv");

      if (
        mapData.status === 200 &&
        dataSummary.status === 200 &&
        dataRaw.status === 200
      ) {
        this.setState({
          dataSummary: dataSummary.data,
          dataRaw: parseRawData(dataRaw.data),
          mapData: mapData.data,
        }, this.updateFilter);
      }

    } catch (e) {
      this.setState({
        feedback: {
          message: e.message,
          variant: "error",
        }
      })
    }
  };

  updateFilter = () => {
    const {
      categorySelected,
      mapData,
      dataSummary,
      dataRaw,
      showImport,
    } = this.state;

    // First get the filter data
    const filterData = getFilterData(
      dataSummary,
      dataRaw,
      categorySelected,
      showImport,
    );

    // Match with map data
    const newMapData = getMapData(
      mapData.features,
      filterData.countries,
      showImport,
    );

    this.setState({
      visData: Object.assign(filterData, {
        features: newMapData.features,
        featuresKey: `${categorySelected}_${showImport}`,
        valueRange: newMapData.valueRange,
        groups: newMapData.groups,
      }),
    });
  };


  /**
   * Close handler for feedback
   */
  handleCloseFeedback = () => {
    this.setState({
      feedback: undefined,
    });
  };

  handleCloseFeaturePopup = () => {
    this.setState({ featureSelected: undefined });
  };

  handleCountrySelect = (country) => {
    const { visData } = this.state;
    const newSelectFeature = visData.features.find(
      ft => ft.properties.ISO_N3 === country.iso,
    );

    if (newSelectFeature !== undefined) {
      this.handleFeatureSelect(newSelectFeature);
    }
  };

  handleFeatureSelect = (feature) => {
    this.setState({
      hoverFeature: undefined,
      selectFeature: feature,
    });
  };

  handleFeatureHover = (feature) => {
    this.setState({
      hoverFeature: feature,
    });
  };

  /**
   * @param {mapboxgl.Map} map
   */
  handleMapLoad = (map) => {
    if (this.state.map === undefined) {
      this.setState({
        map: map,
      });
    }
  };

  handleChangeCategory = (newCategory) => {
    this.setState({
      categorySelected: newCategory,
    }, this.updateFilter);
  };

  /**
   * In case of resize event, this triggers an update of the map size
   */
  handleResize = () => {
    this.setState({
      width: window.innerWidth,
      height: window.innerHeight - 40,
    })
  };

  /**
   * Toggles the sidebar in or out.
   */
  handleSidebarToggle = () => {
    const { sidebarOpen } = this.state;
    this.setState({
      sidebarOpen: !sidebarOpen,
    });
  };

  handleToggleImport = (newShowImport) => {
    this.setState({
      showImport: newShowImport,
    }, this.updateFilter);
  };

  render() {
    const {
      categorySelected,
      height,
      width,
      map,
      mapData,
      dataSummary,
      feedback,
      selectFeature,
      hoverFeature,
      showImport,
      sidebarOpen,
      visData,
    } = this.state;
    const { classes } = this.props;
    const featureInfo = hoverFeature !== undefined
      ? hoverFeature
      : selectFeature !== undefined
        ? selectFeature : undefined;

    return (
      <div className={clsx(classes.root, "pb-trading-map")}>
        <SideBar
          className="pb-sidebar"
          onToggleSideBar={this.handleSidebarToggle}
          open={sidebarOpen}
        >
          <ListCountries
            categories={dataSummary !== undefined ? dataSummary.categories : {}}
            data={visData}
            onChangeCategory={this.handleChangeCategory}
            onChangeImport={this.handleToggleImport}
            onSelectCountry={this.handleCountrySelect}
            countries={visData !== undefined ? visData.countriesList : []}
            selectedCategory={categorySelected}
            selectFeature={selectFeature}
            showImport={showImport}
          />
        </SideBar>
        {
          !sidebarOpen && <SideBarButton className="pb-sidebar-btn" onClick={this.handleSidebarToggle} />
        }
        <main className={clsx(classes.content)}>
          <div className={classes.containerMap}>
            {
              (width > 0 && height > 0 && mapData !== undefined) && (
                <Map
                  data={mapData}
                  height={height}
                  onLoad={this.handleMapLoad}
                  viewport={{
                    bearing: 0,
                    latitude: 51.415,
                    longitude: 0,
                    zoom: 2,
                    pitch: 0,
                  }}
                  width={width}
                >
                  {
                    (map !== undefined && visData !== undefined) && (
                      <DataLayer
                        features={visData.features}
                        featuresKey={visData.featuresKey}
                        hoverFeature={hoverFeature}
                        selectFeature={selectFeature}
                        map={map}
                        onSelect={this.handleFeatureSelect}
                        onHover={this.handleFeatureHover}
                      />
                    )
                  }
                </Map>
              )
            }
          </div>
          { feedback !== undefined && (
            <Snackbar
              className="pb-snackbar-feedback"
              message={feedback.message}
              onClose={this.handleCloseFeedback}
              variant={feedback.variant}
            />
          )}
          {
            featureInfo !== undefined && (
              <FeatureInfo feature={featureInfo} />
            )
          }

          {
            visData !== undefined && (
              <Legende groups={visData.groups} />
            )
          }
          {
            visData !== undefined && (
              <TotalInfo data={visData} />
            )
          }
          <FacebookShareButton
            url={PUBLIC_URL}
            title={"Share on Facebook"}
            className="pb-share-button facebook"
          >
            <FacebookIcon size={40} round />
          </FacebookShareButton>

          <TwitterShareButton
            url={PUBLIC_URL}
            title={"Share on Twitter"}
            className="pb-share-button twitter"
          >
            <TwitterIcon size={40} round />
          </TwitterShareButton>
        </main>
        <div className={classes.footer}>
          <div>
            <a
              href={IMPRESSUM_LINK}
              target="_blank"
              rel="noopener noreferrer"
            >Impressum</a>
            <a
              href={PRIVACY_LINK}
              target="_blank"
              rel="noopener noreferrer"
            >Datenschutz</a>
          </div>
        </div>
        <CookieBannerContainer />
      </div>
    );
  }
}

App.propTypes = {
  classes: PropTypes.object,
};

export default withStyles(styles)(App);
