/**
 * Extracts from a given array set a min max array
 *
 * @param {{ importValue: number, exportValue: number }} countries
 * @param {boolean} showImport
 * @returns {number[]}
 */
export function extractMinMax(countries, showImport) {
  let minMax = [0, 0];
  countries.forEach(
    country => {
      minMax = [
        Math.min(minMax[0], showImport ? country.importValue : country.exportValue),
        Math.max(minMax[1], showImport ? country.importValue : country.exportValue),
      ]
    }
  );
  return minMax;
}

/**
 * Layout of the programs.
 * @param {{
 *   className: string
 *   onSelectProgram: Function,
 *   programs: {{ id: string, title: string, subtitle: string, sumTotal: number }[]} programs
 * }}
 * @returns {*}
 * @constructor
 */
export default function ListCountries({
  categories,
  className,
  data,
  onChangeCategory,
  onChangeImport,
  onSelectCountry,
  selectedCategory,
  selectFeature,
  showImport,
}) {
  const classes = useStyles();
  const scaleRadius = scaleSqrt()
    .domain([0, extractMinMax(data.countriesList, showImport)[1]])
    .range([0, 100]);

  /**
   * Sorting functions which sorts the given array by the aum property.
   * @param {{ importValue: number, exportValue: number }} a
   * @param {{ importValue: number, exportValue: number }} b
   * @returns {number}
   */
  function sortBySum(a, b) {
    const aSum = showImport ? a.importValue : a.exportValue;
    const bSum = showImport ? b.importValue : b.exportValue;
    return aSum < bSum
      ? 1
      : aSum > bSum
        ? -1
        : 0;
  }

  // Scroll to list feature
  useEffect(() => {
    if (selectFeature !== undefined) {
      const listFeature = data.countriesList.find(
        ft => ft.iso === selectFeature.properties.ISO_N3
      );

      if (listFeature !== undefined && listFeature.ref.current !== null) {
        // scroll to feature
        listFeature.ref.current.scrollIntoView({
          behavior: "smooth",
          block: "center",
        });
      }
    }
  });

  return (
    <div className={clsx(classes.root, className)}>
      <div className={classes.titleContainer}>
        <Typography component="h5" variant="h5">
          Auenhandel Deutschland
        </Typography>
        <Typography component="span" variant="body2">
          Im- und Exporte von Deutschland in EURO fr das Jahr 2019. Kategorisiert nach Warensystematik:
        </Typography>
      </div>
      <div className={classes.selectContainer}>
        <FilterCategory
          categories={categories}
          onChange={onChangeCategory}
          selectedCategory={selectedCategory}
        />
      </div>
      <div className={classes.controlContainer}>
        <ButtonGroup color="primary" aria-label="Sortieren nach:">
          <Button
            edge="start"
            className={showImport ? "selected" : ""}
            color="inherit"
            aria-label="Importe"
            disableRipple={true}
            disableFocusRipple={true}
            onClick={() => onChangeImport(true)}
          >IMPORTE</Button>
          <Button
            edge="start"
            className={!showImport ? "selected" : ""}
            color="inherit"
            aria-label="Exporte"
            disableRipple={true}
            disableFocusRipple={true}
            onClick={() => onChangeImport(false)}
          >EXPORTE</Button>
        </ButtonGroup>
      </div>
      <div className={classes.listContainer}>
        <div>
      <ul className={classes.contentList}>
        {
          data.countriesList
            .filter(c => (showImport ? c.importValue : c.exportValue) > 0)
            .sort(sortBySum)
            .map(
              (country) => {
                const { iso, name, ref, importValue, exportValue } = country;
                const sum = showImport ? importValue : exportValue;
                const width = sum > 0
                  ? Math.min(100, 2 + scaleRadius(sum))
                  : 0;
                const bilance = exportValue - importValue;
                const textPrimary = showImport
                  ? `I: ${beautifyNumber(sum)} `
                  : `E: ${beautifyNumber(sum)} `;
                const textSecondary = showImport
                  ? `\\ E: ${beautifyNumber(exportValue)} \\ : ${beautifyNumber(bilance)} in TEUR`
                  : `\\ I: ${beautifyNumber(importValue)} \\ : ${beautifyNumber(bilance)} in TEUR`;
                const share = showImport
                  ? round((sum / data.sumTotalImport) * 100, 2)
                  : round((sum / data.sumTotalExport) * 100, 2);

                return (
                  <React.Fragment key={iso}>
                    <li
                      ref={ref}
                      onClick={() => onSelectCountry(country)}
                      className={
                        selectFeature !== undefined && selectFeature.properties.ISO_N3 === iso
                          ? "hover"
                          : ""
                      }
                    >
                      <Typography component="span" variant="overline">
                        {share}%
                      </Typography>
                      <Typography component="span" variant="subtitle1">
                        {name}
                      </Typography>
                      <Typography component="span" variant="body2">
                        <span className="primary">{textPrimary}</span>
                        <span className="secondary">{textSecondary}</span>
                      </Typography>
                      <div className={classes.listBar}>
                        <div className={showImport ? "import" : "export"} style={{ width: `${width}%` }} />
                      </div>
                    </li>
                    <Divider />
                  </React.Fragment>

                )
              }
            )
        }
      </ul>
        </div>
      </div>
    </div>
  );
}

ListCountries.defaultProps = {
  categories: {},
  className: "pb-list-countries",
  data: {
    sumTotalImport: 0,
    sumTotalExport: 0,
    countriesList: [],
  },
  onSelectCountry: () => {},
};

ListCountries.propTypes = {
  categories: PropTypes.object,
  className: PropTypes.string,
  data: PropTypes.shape({
    sumTotalImport: PropTypes.number,
    sumTotalExport: PropTypes.number,
    countriesList: PropTypes.arrayOf(
      PropTypes.shape({
        iso: PropTypes.string,
        name: PropTypes.string,
        sum: PropTypes.number,
      }),
    )
  }),
  onSelectCountry: PropTypes.func,
  onChangeCategory: PropTypes.func,
  onChangeImport: PropTypes.func,
  selectedCategory: PropTypes.string,
  selectFeature: PropTypes.object,
  showImport: PropTypes.bool,
};
