import React from "react";
import PropTypes from "prop-types";
import clsx from "clsx";
import { makeStyles } from "@material-ui/core/styles";
import IconButton from "@material-ui/core/IconButton";
import MenuIcon from "@material-ui/icons/Menu";
import { iconSize } from "../../settings";

const useStyles = makeStyles(theme => ({
  root: {
    display: "block",
    zIndex: 1010,
    position: "absolute",
    top: theme.spacing(1),
    left: theme.spacing(2),
    padding: theme.spacing(2),
    height: 56,
    width: 56,
  },
  button: {
    boxShadow: "0 4px 5px 0 rgba(0,0,0,0.14), 0 1px 10px 0 rgba(0,0,0,0.12), 0 2px 4px -1px rgba(0,0,0,0.2)",
    backgroundColor: "#0C3D7E",
    color: "#FFFFFF",
    '&:hover': {
      backgroundColor: "#002465",
    },
  }
}));

/**
 * SideBar button
 * @param {{
 *   className: string
 *   onClick: Function,
 * }}
 * @returns {*}
 * @constructor
 */
export default function SideBarButton({ className,  onClick }) {
  const classes = useStyles();
  return (
    <div className={clsx(classes.root, className)}>
      <IconButton
        className={classes.button}
        aria-label="Toggle Sidebar"
        edge="start"
        color="inherit"
        onClick={onClick}
      >
        <MenuIcon style={{ width: iconSize, height: iconSize }} />
      </IconButton>
    </div>
  );
}

SideBarButton.defaultProps = {
  className: "pb-sidebar-btn",
};

SideBarButton.propTypes = {
  className: PropTypes.string,
  onClick: PropTypes.func,
};
