import React, { Component } from "react";
import PropTypes from "prop-types";
import mapboxgl from "mapbox-gl";
import "mapbox-gl/dist/mapbox-gl.css";
import ZoomToExtentControl from "./ZoomToExtentControl/ZoomToExtentControl";

export default class Map extends Component {
  constructor(props) {
    super(props);

    this.state = {
      // map object
      map: undefined,

      // reference to the map container, controlled by mapbox
      refMap: React.createRef(),
    };
  }

  componentDidMount() {
    const { refMap } = this.state;
    const {
      data,
      viewport,
    } = this.props;

    // initalize the map object
    const map = new mapboxgl.Map({
      bearing: viewport.bearing,
      center: [viewport.longitude, viewport.latitude],
      container: refMap.current,
      pitch: viewport.pitch,
      //style: undefined,
      style: {
        "version": 8,
        "name": "blank",
        "sources": {
          "blank": {
            "type": "vector",
            "url": ""
          }
        },
        "layers": [{
          "id": "background",
          "type": "background",
          "paint": {
            "background-color": "#cad2d3"
          }
        }]
      },
      zoom: viewport.zoom,
    });

    // disable map rotation using touch rotation gesture
    // disable map rotation using right click + drag
    map.dragRotate.disable();
    map.touchZoomRotate.disableRotation();

    map.addControl(new mapboxgl.NavigationControl({ showCompass: false }));
    map.addControl(new ZoomToExtentControl([-180,-90, 180, 90]));

    // update the state after initial load
    map.on("load", () => {
      this.setState({
        map: map,
      });

      //
      // Add base layer
      //
      const sourceId = "source-id-baselayer";
      const layerIdFill = "layer-id-baselayer-fill";
      map.addSource(sourceId, {
        "type": "geojson",
        "data": data,
      });

      map.addLayer({
        "id": layerIdFill,
        "type": "fill",
        "source": sourceId,
        "layout": {},
        "paint": {
          "fill-color": "#f6f6f4",
          "fill-outline-color": "#d6d6d6"
        }
      });

      if (this.props.onLoad) {
        this.props.onLoad(map);
      }
    });

  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    const { map } = this.state;
    const { width, height } = this.props;

    if (
      map !== undefined && (
        width !== prevProps.width ||
        height !== prevProps.height
      )
    ) {
      // Resize the map container
      map.resize();
    }
  }

  componentWillUnmount() {
    const { map } = this.state;

    if (map !== undefined) {
      map.remove();
    }
  }

  render() {
    const { refMap } = this.state;
    const { children, height, width } = this.props;

    return (
      <div ref={refMap} style={{ width: `${width}px`, height: `${height}px` }}>
        {children}
      </div>
    );
  }
}

Map.propTypes = {
  data: PropTypes.any.isRequired,
  height: PropTypes.number,
  onLoad: PropTypes.func,
  viewport: PropTypes.shape({
    bearing: PropTypes.number,
    latitude: PropTypes.number,
    longitude: PropTypes.number,
    zoom: PropTypes.number,
    pitch: PropTypes.number
  }),
  width: PropTypes.number,
};
