import React, { Component } from "react";
import PropTypes from "prop-types";
import clsx from "clsx";
import { makeStyles } from "@material-ui/core/styles";
import InputLabel from "@material-ui/core/InputLabel";
import FormControl from "@material-ui/core/FormControl";
import Select from "@material-ui/core/Select";
import { withStyles} from "@material-ui/core";


const useStyles = makeStyles(theme => ({
  root: {
    "& > p": {
      fontSize: 12,
      color: "rgba(0,0,0,0.6)",
    },
  },
  formControl: {
    backgroundColor: "#FFFFFF",
    minWidth: 120,
  },
  selectEmpty: {
    marginTop: theme.spacing(2),
  },
}));


/**
 * Filter category component
 */
function FilterCategory(props) {
  const {
    className,
    categories,
    selectedCategory,
  } = props;

  const handleChange = event => {
    props.onChange(event.target.value);
  };

  const classes = useStyles();
  const inputLabel = React.useRef(null);
  const [labelWidth, setLabelWidth] = React.useState(0);
  React.useEffect(() => {
    setLabelWidth(inputLabel.current.offsetWidth);
  }, []);

  // Build options
  const newOptions = Object.keys(categories).map(
    key => {
      return {label: categories[key], value: key};
    }
  ).sort(
    (a, b) => {
      return a.label > b.label
        ? 1
        : a.label < b.label
          ? -1
          : 0
    }
  );

  const options = [
    ...[{ label: "All", value: "All"}],
    ...newOptions,
  ];

  return (
    <div className={clsx(classes.root, "pb-select-networks", className)}>
      <FormControl variant="outlined" className={classes.formControl}>
        <InputLabel ref={inputLabel} htmlFor="select-network">
          Kategorie
        </InputLabel>
        <Select
          native
          value={selectedCategory}
          onChange={handleChange}
          labelWidth={labelWidth}
        >
          {
            options.map(
              (category, index) => (
                <option key={index} value={category.value}>{category.label}</option>
              )
            )
          }
        </Select>
      </FormControl>
    </div>
  );
}

FilterCategory.defaultProps = {
  className: "",
  categories: {},
  onChange: () => {},
  selectedCategory: "",
};

FilterCategory.propTypes = {
  className: PropTypes.string,
  categories: PropTypes.object,
  onChange: PropTypes.func,
  selectedCategory: PropTypes.string,
};

export default FilterCategory;
