import React from "react";
import PropTypes from "prop-types";
import clsx from "clsx";
import { makeStyles } from "@material-ui/core/styles";
import IconButton from "@material-ui/core/IconButton";
import Drawer from "@material-ui/core/Drawer";
import MenuIcon from "@material-ui/icons/Menu";
import {
  fontFamily,
  iconSize,
  sidebarWidth,
} from "../../settings";
import BackSvg from "./Back.svg";
import "./SideBar.scss";
import Logo from "../../icons/pikobytes-logo-white.png";

const useStyles = makeStyles(theme => ({
  drawer: {
    width: sidebarWidth,
    flexShrink: 0,
  },
  drawerPaper: {
    width: sidebarWidth,
  },
  // Header Container
  headerContainer: {
    height: 56,
    width: "100%",
    display: "block",
    padding: theme.spacing(1,1.75,0,1),
  },
  containerLogo: {
    position: "absolute",
    top: 10,
    left: 10,
    zIndex: 1020,
    "& img": {
      width: 250,
    }
  },
  btnBack: {
    color: "rgba(0,0,0,0.54)",
    marginLeft: 0,
    top: 15,
    "& img": {
      marginRight: theme.spacing(1),
    },
    "& span": {
      color: "#0C3D7E",
      fontFamily: fontFamily,
      fontSize: 14,
      fontWeight: 500,
      letterSpacing: 1.25,
      lineHeight: "16px",
      display: "inline-block",
      margin: 0,
      verticalAlign: "super",
    }
  },
  btnToggleSidebar: {
    float: "right",
    top: 15,
  },
  // Header Container End
  contentContainer: {
    position: "absolute",
    top: theme.spacing(14),
    bottom: 0,
    width: "100%",
    "& > div": {
      height: "100%",
    }
  }
}));

/**
 * Basic Sidebar Layout.
 * @param {{
 *   className: string,
 *   onCloseDetails: Function|undefined,
 *   onToggleSideBar: Function,
 *   open: boolean,
 * }}
 * @returns {*}
 * @constructor
 */
export default function SideBar({
  children,
  className,
  onCloseDetails,
  onToggleSideBar,
  open,
}) {
  const classes = useStyles();
  return (
    <Drawer
      className={clsx(classes.drawer, "pb-sidebar-default", className)}
      variant="persistent"
      anchor="left"
      open={open}
      classes={{
        paper: classes.drawerPaper,
      }}
    >
      <div className={classes.headerContainer}>
        <div className={classes.containerLogo}>
          <a href="https://www.pikobytes.de/" target="_blank" rel="noopener">
            <img src={Logo} alt=""  />
          </a>
        </div>
        {
          onCloseDetails && (
            <IconButton
              edge="start"
              color="inherit"
              aria-label="Zurck zur bersicht"
              className={classes.btnBack}
              onClick={onCloseDetails}
              style={{ backgroundColor: 'transparent' }}
              disableRipple={true}
              disableFocusRipple={true}
            >
              <img src={BackSvg} alt="" width="24" height="24" />
              <span>BERSICHT</span>
            </IconButton>
          )
        }
        <IconButton
          aria-label="menu"
          edge="start"
          color="inherit"
          className={classes.btnToggleSidebar}
          onClick={onToggleSideBar}
        >
          <MenuIcon style={{ width: iconSize, height: iconSize }}/>
        </IconButton>
      </div>
      <div className={classes.contentContainer}>
        {children}
      </div>
    </Drawer>
  );
}

SideBar.defaultProps = {
  className: "pb-sidebar",
  onToggleSideBar: () => {},
  open: true,
};

SideBar.propTypes = {
  className: PropTypes.string,
  onCloseDetails: PropTypes.func,
  onToggleSideBar: PropTypes.func,
  open: PropTypes.bool,
};

