import React, { Component } from "react";
import PropTypes from "prop-types";
import clsx from "clsx";
import { withStyles} from "@material-ui/core";
import {beautifyNumber} from "../structs/utils";
import {fontFamily} from "../settings";

const styles = (theme) => {
  return {
    root: {
      width: "auto",
      backgroundColor: "#FFFFFF",
      boxShadow: "0 2px 2px 0 rgba(0,0,0,0.14), 0 3px 1px -2px rgba(0,0,0,0.12), 0 1px 5px 0 rgba(0,0,0,0.2)",
      padding: theme.spacing(1),
      "& > p": {
        fontSize: 12,
        color: "rgba(0,0,0,0.6)",
      },
    },
    propertyContainer: {
      "& .label": {
        height: 28,
        color: "rgba(0,0,0,0.6)",
        fontFamily: fontFamily,
        fontSize: 16,
        fontWeight: 500,
        letterSpacing: 0.1,
        lineHeight: "20px",
        display: "block",

      },
      "& > div": {
        height: 30,
      },
      "& .text": {
        height: 24,
        color: "rgba(0,0,0,0.6)",
        fontFamily: fontFamily,
        fontSize: 14,
        letterSpacing: 0.25,
        lineHeight: "20px",
        display: "block",
        overflow: "hidden",
        whiteSpace: "nowrap",
        textOverflow: "ellipsis",
      }
    },
  }
};

/**
 * Map legend component
 */
class FeatureInfo extends Component {
  render() {
    const {
      className,
      classes,
      data,
    } = this.props;

    return (
      <div className={clsx(classes.root, "pb-total-info", className)}>
        <div className={classes.propertyContainer}>
          <span className="label">bersicht Deutschland:</span>
          <span className="text">Importe: {beautifyNumber(data.sumTotalImport)} TEUR</span>
          <span className="text">Exporte: {beautifyNumber(data.sumTotalExport)} TEUR</span>
          <span className="text">Bilanz: {beautifyNumber(data.sumTotalExport - data.sumTotalImport)} TEUR</span>
        </div>
      </div>
    );
  }
}

FeatureInfo.defaultProps = {
  className: "",

};

FeatureInfo.propTypes = {
  classes: PropTypes.object,
  className: PropTypes.string,
  data: PropTypes.shape({
    sumTotalImport: PropTypes.number,
    sumTotalExport: PropTypes.number,
  }),
};

export default withStyles(styles)(FeatureInfo);
