import "./ZoomToExtentControl.scss";

/**
 * Control for zooming to the max extent
 */
export default class ZoomToExtentControl {
  constructor(extent) {
    this.extent = extent;
  }

  onAdd(map){
    this.map = map;

    this.container = document.createElement("div");
    this.container.className = "mapboxgl-ctrl pb-zoom-to-extent-ctl";

    // create the button
    const button = document.createElement("button");
    button.className = "mapboxgl-ctrl-icon";
    button.type = "button";
    button.title = "Zoom To Extent";
    button.setAttribute("aria-label", "Zoom To Extent");
    this.container.appendChild(button);

    // add event listener
    button.addEventListener("click", () => {
      const ext = this.extent;
      map.fitBounds([
        [ext[0], ext[1]],
        [ext[2], ext[3]]
      ]);
    });

    return this.container;
  }

  onRemove(){
    this.container.parentNode.removeChild(this.container);
    this.map = undefined;
  }
}
