import React, { Component } from "react";
import PropTypes from "prop-types";
import mapboxgl from "mapbox-gl";
import { createGeoJsonCollection } from "../structs/utils";
import extent from "turf-extent";

// IDs used for source and layers
const SOURCE_ID_POLYGON = "data-source-1";
const LAYER_ID_POLYGON = "data-layer-polygon-1";
const LAYER_ID_POLYGON_HIGHLIGHT = "data-layer-polygon-2";

/**
 * @param {{ properties: {ISO_N3: string} }} ft1
 * @param {{ properties: {ISO_N3: string} }} ft2
 * @returns {boolean}
 */
function assertSameFeature(ft1, ft2) {
  return ft1.properties.ISO_N3 === ft2.properties.ISO_N3
}

class DataLayer extends Component {
  constructor(props) {
    super(props);

    this.state = {
      popup: undefined,
      selected: undefined,
    }
  }

  componentDidMount() {
    const {
      features,
      map,
    } = this.props;

    // add the data
    map.addSource(SOURCE_ID_POLYGON, {
      "type": "geojson",
      "data": createGeoJsonCollection(features),
    });

    //
    // Add layers for displaying the countries
    //
    map.addLayer({
      "id": LAYER_ID_POLYGON,
      "type": "fill",
      "source": SOURCE_ID_POLYGON,
      "layout": {},
      "paint": {
        "fill-color": ["get", "color"],
        "fill-opacity": 0.8,
        "fill-outline-color": "#777777"
      }
    });
    map.addLayer({
      "id": LAYER_ID_POLYGON_HIGHLIGHT,
      "type": "line",
      "source": SOURCE_ID_POLYGON,
      "paint": {
        "line-color": "#FF0000",
        "line-width": 2
      }
    });

    // register event listeners
    map.on("mousemove", LAYER_ID_POLYGON, this.handleMouseMove);
    map.on("mouseleave", LAYER_ID_POLYGON, this.handleMouseLeave);
    map.on("click", this.handleClick);

    // Reset the filter
    this.resetFilter(map);
  }

  componentWillUnmount() {
    const { map } = this.props;
    map.off("mousemove", LAYER_ID_POLYGON, this.handleMouseMove);
    map.off("mouseleave", LAYER_ID_POLYGON, this.handleMouseLeave);
    map.off("click", this.handleClick);
  }

  componentDidUpdate(prevProps) {
    const {
      features,
      featuresKey,
      map,
      selectFeature,
    } = this.props;

    if (featuresKey !== prevProps.featuresKey) {
      map.getSource(SOURCE_ID_POLYGON).setData(
        createGeoJsonCollection(features),
      );
    }

    // Update filter
    if (map !== undefined) {
      this.resetFilter(map);
    }

    if (
      (selectFeature !== undefined && prevProps.selectFeature === undefined) ||
      (selectFeature !== undefined && prevProps.selectFeature !== undefined && !assertSameFeature(selectFeature, prevProps.selectFeature)) ||
      (selectFeature === undefined && prevProps.selectFeature !== undefined)
    ) {
      this.focusFeature();
      return;
    }
  }

  /**
   * Returns a feature. MapboxGL features are partly broken, therefor we use
   * the given id of a feature to extract the feature from the raw data.
   * @param {{ properties: { ISO_N3: string } }} ft
   * @returns {*}
   */
  getFeature = (ft) => {
    return Object.values(this.props.features).find(v => assertSameFeature(v, ft));
  };

  /**
   * Handler for processing the click on a feature.
   * @param {{
   *   point: [*],
   *   target: mapboxgl.Map,
   * }} e
   */
  handleClick = (e) => {
    const features = e.target.queryRenderedFeatures(e.point);

    if (features.length > 0) {
      // Set select feature and reset hover. When we click of an feature we can
      // expected that this feature is already hovered & sync
      const newSelectFeature = this.getFeature(features[0]);

      // dispatch select
      this.props.onSelect(newSelectFeature);
    } else {
      // dispatch select
      this.props.onSelect(undefined);
    }
  };

  /**
   * Handler for processing the pointer enter from a feature
   * @param {{
   *   features: [*]
   * }} e
   */
  handleMouseMove = (e) => {
    const {
      hoverFeature,
      map,
      selectFeature,
    } = this.props;

    // update the cursor
    map.getCanvas().style.cursor = "pointer";

    if (e.features.length > 0) {
      const newHoverFeature = this.getFeature(e.features[0]);

      if (
        (selectFeature === undefined || newHoverFeature.properties.ISO_N3 !== selectFeature.properties.ISO_N3) &&
        (hoverFeature === undefined || newHoverFeature.properties.ISO_N3 !== hoverFeature.properties.ISO_N3) &&
        newHoverFeature.properties.ISO_N3 !== "276"
      ) {
        // Set hover & snyc
        this.props.onHover(newHoverFeature);
      } else if (
        selectFeature !== undefined && newHoverFeature.properties.ISO_N3 === selectFeature.properties.ISO_N3
      ) {
        this.props.onHover(undefined);
      }
    }
  };

  /**
   * Handler for processing the pointer leave fromfeature
   */
  handleMouseLeave = () => {
    // Reset hover & snyc
    this.props.onHover(undefined);
  };

  focusFeature = () => {
    const {
      map,
      selectFeature,
    } = this.props;

    if (selectFeature !== undefined) {
      // extract all geometries associated with this feature, calculate the extent
      // and focus it
      const bbox = extent(createGeoJsonCollection([selectFeature]));
      map.fitBounds([
        [bbox[0], bbox[1]],
        [bbox[2], bbox[3]],
      ], {
        padding: {top: 100, bottom:100, left: 450, right: 100}
      });
    }
  };

  /**
   * Reset all filters
   * @param {mapboxgl.Map|undefined} map
   */
  resetFilter = (map) => {
    if (map !== undefined) {
      const { selectFeature, hoverFeature } = this.props;

      const filterIds = [];
      if (selectFeature !== undefined) filterIds.push(selectFeature.properties.ISO_N3);
      if (hoverFeature !== undefined && !filterIds.includes(hoverFeature.properties.ISO_N3)) {
        filterIds.push(hoverFeature.properties.ISO_N3);
      }

      // create new filter
      const newFilter = filterIds.length > 0
        ? ["in", "ISO_N3", ...filterIds]
        : ["in", "ISO_N3"];
      map.setFilter(LAYER_ID_POLYGON_HIGHLIGHT, newFilter);
    }
  };

  render() {
    return (
      <div style={{ display: "none" }} />
    );
  }
}

DataLayer.defaultProps = {
  onClick: () => {},
};

DataLayer.propTypes = {
  features: PropTypes.array.isRequired,
  featuresKey: PropTypes.string.isRequired,
  hoverFeature: PropTypes.object,
  map: PropTypes.instanceOf(mapboxgl.Map).isRequired,
  onHover: PropTypes.func.isRequired,
  onSelect: PropTypes.func.isRequired,
  selectFeature: PropTypes.object,
};

export default DataLayer;
