import React, { Component } from "react";
import PropTypes from "prop-types";
import clsx from "clsx";
import Typography from "@material-ui/core/Typography";
import { withStyles} from "@material-ui/core";
import { beautifyNumber } from "../structs/utils";

const styles = (theme) => {
  return {
    root: {
      width: 300,
      backgroundColor: "#FFFFFF",
      boxShadow: "0 2px 2px 0 rgba(0,0,0,0.14), 0 3px 1px -2px rgba(0,0,0,0.12), 0 1px 5px 0 rgba(0,0,0,0.2)",
      padding: theme.spacing(1),
      "& > p": {
        fontSize: 12,
        color: "rgba(0,0,0,0.6)",
      },
    },
    containerHover: {
      color: "rgba(0,0,0,0.6)",
      "& p": {
        marginBottom: theme.spacing(1),
        fontSize: 16,
        fontWeight: 500,
      },
      "& span": {
        display: "block",
      }
    }
  }
};

/**
 * Feature info for giving feedback about the current feature
 */
class FeatureInfo extends Component {
  render() {
    const {
      className,
      classes,
      feature,
    } = this.props;

    return (
      <div className={clsx(classes.root, "pb-feature-info", className)}>
        {
          (feature !== undefined) && (
            <React.Fragment>
              <div className={classes.containerHover}>
                <Typography component="p" variant="body1">{feature.properties.name}</Typography>
                <Typography component="span" variant="body2">Import: {beautifyNumber(feature.properties.importValue)} TEUR</Typography>
                <Typography component="span" variant="body2">Export: {beautifyNumber(feature.properties.exportValue)} TEUR</Typography>
                <Typography component="span" variant="body2">berschuss: {beautifyNumber(feature.properties.exportValue - feature.properties.importValue)} TEUR</Typography>
              </div>
            </React.Fragment>
          )
        }
      </div>
    );
  }
}

FeatureInfo.defaultProps = {
  className: "",

};

FeatureInfo.propTypes = {
  classes: PropTypes.object,
  className: PropTypes.string,
  feature: PropTypes.shape({
    properties: PropTypes.shape({
      name: PropTypes.string,
      importValue: PropTypes.number,
      exportValue: PropTypes.number,
    }),
  }),
};

export default withStyles(styles)(FeatureInfo);
