import React, { Component } from "react";
import PropTypes from "prop-types";
import clsx from "clsx";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import Typography from "@material-ui/core/Typography";
import { withStyles} from "@material-ui/core";

const styles = (theme) => {
  return {
    root: {
      width: 200,
      backgroundColor: "#FFFFFF",
      boxShadow: "0 2px 2px 0 rgba(0,0,0,0.14), 0 3px 1px -2px rgba(0,0,0,0.12), 0 1px 5px 0 rgba(0,0,0,0.2)",
      padding: theme.spacing(0.5),
      "& > p": {
        fontSize: 12,
        color: "rgba(0,0,0,0.6)",
      },
    },
    containerList: {
      "&.MuiList-padding": {
        padding: 0,
      },
      "& .MuiListItem-root": {
        padding: theme.spacing(0.5, 1, 0.5, 1),
      },
      "& .MuiListItemIcon-root": {
        minWidth: 24,
      },
      "& .MuiListItemText-primary": {
        fontSize: 10,
      }
    },
    icon: {
      width: 12,
      height: 12,
    },
  }
};

/**
 * Map legend component
 */
class FeatureInfo extends Component {
  render() {
    const {
      className,
      classes,
      groups,
    } = this.props;

    return (
      <div className={clsx(classes.root, "pb-map-legend", className)}>
        <Typography component="p" variant="body1">
          Legende:
        </Typography>
        <List className={classes.containerList}>
          {
            groups.slice(1).map(
              (group, index) => (
                <ListItem key={index}>
                  <ListItemIcon>
                    <div className={classes.icon} style={{ backgroundColor: group.color }} />
                  </ListItemIcon>
                  <ListItemText
                    primary={group.label}
                  />
                </ListItem>
              )
            )
          }
        </List>
        <Typography component="p" variant="body1">
           Statistisches Bundesamt (Destatis), 2020. Stand: 01.04.2020.
        </Typography>
      </div>
    );
  }
}

FeatureInfo.defaultProps = {
  className: "",

};

FeatureInfo.propTypes = {
  classes: PropTypes.object,
  className: PropTypes.string,
  groups: PropTypes.arrayOf(
    PropTypes.shape({
      color: PropTypes.string,
      label: PropTypes.string,
    })
  ),
};

export default withStyles(styles)(FeatureInfo);
