import React from "react";
import { dsvFormat } from "d3-dsv";
import clonedeep from "lodash.clonedeep";
import round from "lodash.round";

const COLOR_IMPORT = [
  "#f6f6f4",
  "#ffd6d0",
  "#ffbeb5",
  "#fea59a",
  "#fb8b80",
  "#f67168",
  "#ef5350",
];

const COLOR_EXPORT = [
  "#f6f6f4",
  "#cbe4ff",
  "#afd1f5",
  "#94beeb",
  "#77abe1",
  "#5899d7",
  "#3087cd",
];

/**
 * Builds a better looking string from a number.
 * @param {number} x
 * @returns {string}
 */
export function beautifyNumber(x) {
  return x === undefined || x === null || isNaN(x)
    ? ""
    : Math.round(x).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".");
}

/**
 * @param {[*]}features
 * @returns {{features: [*], type: string}}
 */
export function createGeoJsonCollection(features) {
  return {
    type: "FeatureCollection",
    features: features,
  }
}

/**
 * Creates a new filter
 * @param {{
 *   countries: Object,
 *   totalExportEuro: number,
 *   totalImportEuro: number,
 *   totalExportPerCountryEuro: Object
 *   totalImportPerCountryEuro: Object,
 * }} dataSummary
 * @param {[{
 *   export_value_euro: number,
 *   gp2019_code: number,
 *   iso: string,
 *   import_value_euro: number,
 * }]} dataRaw
 * @param {string} categorySelected
 * @param {boolean} showImport
 * @returns {{
 *   sumTotalExport: number,
 *   sumTotalImport: number,
 *   countries: [*],
 * }}
 */
export function getFilterData(
  dataSummary,
  dataRaw,
  categorySelected,
  showImport,
) {
  let response = {
    sumTotalImport: dataSummary.totalImportEuro,
    sumTotalExport: dataSummary.totalExportEuro,
    countries: dataSummary.importExportPerCountry,
  };

  if (categorySelected !== "All") {
    // Extract the values from the raw data
    const _processData = [];
    dataRaw.forEach(
      (d) => {
        const {
          gp2019_code,
          import_value_euro,
          export_value_euro,
          iso
        } = d;

        if (gp2019_code === categorySelected) {
          _processData.push({
            iso: iso,
            importValue: import_value_euro,
            exportValue: export_value_euro,
          });
        }
      }
    );

    // Calculate sums and prepare output data
    let sumTotalImport = 0;
    let sumTotalExport = 0;
    const countries = {};
    _processData.forEach(
      (d) => {
        const {
          iso,
          importValue,
          exportValue
        } = d;
        sumTotalExport += exportValue;
        sumTotalImport += importValue;
        countries[iso] = {
          name: dataSummary.countries[iso],
          importValue: importValue,
          exportValue: exportValue,
        };
      }
    );

    response = {
      sumTotalImport: sumTotalImport,
      sumTotalExport: sumTotalExport,
      countries: countries,
    };
  }

  // Add a countries array for list visualization
  const countriesList = [];
  Object.keys(response.countries).forEach(
    key => {
      countriesList.push({
        iso: key,
        name: dataSummary.countries[key],
        ref: React.createRef(),
        importValue: response.countries[key].importValue,
        exportValue: response.countries[key].exportValue,
      });
    }
  );

  return Object.assign(response, { countriesList: countriesList });
}

/**
 * @param {number} value
 * @param {[{ color: string, range: number[]}]} groups
 * @return {string}
 */
function categoryScale(value, groups) {
  if (value === 0 || value === NaN) {
    return groups[0].color;
  } else {
    for (let i = 1; i < groups.length; i++) {
      if (value >= groups[i].range[0] && value < groups[i].range[1]) {
        return groups[i].color;
      } else if (value >= groups[i].range[1] && i === groups.length - 1) {
        return groups[i].color
      }
    }
  }
  return groups[0].color;
}

/**
 * @param {[*]} features
 * @param {Object} countries
 * @param {boolean} showImport
 * @returns {{features: *, valueRange: *[], groups: {color: string, range: number[], label: string}[]}}
 */
export function getMapData(features, countries, showImport) {
  let maxValue = 0;

  // Match with map data
  const newFeatures = features.map(
    ft => {
      const newFt = clonedeep(ft);
      const d = countries[newFt.properties.ISO_N3];
      newFt.properties.sum = d !== undefined
        ? showImport
          ? d.importValue
          : d.exportValue
        : 0;

      // update max value
      maxValue = Math.max(maxValue, newFt.properties.sum);

      if (d !== undefined) {
        newFt.properties.importValue = d.importValue;
        newFt.properties.exportValue = d.exportValue;
        newFt.properties.name = d.name;
      }

      return newFt;
    }
  );

  // Calculate the categories
  const colorScale = showImport ? COLOR_IMPORT : COLOR_EXPORT;
  const stepCount = colorScale.length - 1;
  const maxVal = round(maxValue, -(`${maxValue}`.length - 2));
  const _s = Math.ceil(maxVal / stepCount);
  const steps = round(_s, -(`${_s}`.length > 3 ? `${_s}`.length - 2 : -1));
  const groups = [{ range: [0, 0], label: "", color: colorScale[0] }];
  let currentStepValue = 0;
  for (let i = 1; i < colorScale.length; i++) {
    const newStepValue = currentStepValue + steps;
    groups.push({
      range: [currentStepValue, newStepValue],
      label: i === 1
        ? `< ${beautifyNumber(newStepValue)} TEUR`
        : i === colorScale.length - 1
          ? `> ${beautifyNumber(currentStepValue)} TEUR`
          : `${beautifyNumber(currentStepValue)} - ${beautifyNumber(newStepValue)} TEUR`,
      color: colorScale[i],
    });
    currentStepValue = newStepValue;
  }

  // Add color scales to the features
  const featuresWithColor = newFeatures.map(
    ft => {
      ft.properties.color = ft.properties.ISO_N3 !== "276"
        ? ft.properties.sum !== undefined
          ? categoryScale(ft.properties.sum, groups)
          : "#f6f6f4"
        : "#777777";
      return ft;
    }
  );

  return {
    features: featuresWithColor,
    valueRange: [0, currentStepValue],
    groups: groups,
  };
}

/**
 * @param content
 * @returns {*}
 */
export function parseRawData(content) {
  const data = dsvFormat(";").parse(content);
  return data.map(
    r => {
      return {
        year: parseInt(r["year"]),
        country: r["country"],
        gp2019_code: r["gp2019_code"],
        gp2019_name: r["gp2019_name"],
        export_weight_tonne: parseFloat(r["export_weight_tonne"]),
        export_value_euro: parseFloat(r["export_value_euro"]),
        import_weight_tonne: parseFloat(r["import_weight_tonne"]),
        import_value_euro: parseFloat(r["import_value_euro"]),
        iso: r["iso"],
      };
    }
  );
}
